.App {
  text-align: center;
  background-color: antiquewhite;
}
 li , ul ,button ,h4 , p {
  padding: 0;
  margin: 0;
  list-style: none;
  display: block;
  border: none;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.lds-ring {
  display: inline-block;
  position: relative;
  background-color: red;
  top: 50%;  /* position the top  edge of the element at the middle of the parent */
  left: 50%; /* position the left edge of the element at the middle of the parent */
  transform: translate(-50%, -50%);
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: red blue violet green;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


.gallery-list {
  padding: 40px;
  display: grid;
  grid-template-columns: repeat(3, 300px);
  grid-gap: 30px 10px;
  justify-content: center;
}
















.modal {
  position: absolute; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; 
}
.close-modal-button {
  background-color: transparent;
  margin-left: auto;
}

.close-modal-button:after {
  content: ' \00D7';
  font-size: 36px;
}

.modal .item-container  {
  position: absolute; /* Stay in place */
  z-index: 4; /* Sit on top */
  background-color: white;
  padding: 20px;
  width: 800px;
  height: 510px;
  top: 50%;  /* position the top  edge of the element at the middle of the parent */
  left: 50%; /* position the left edge of the element at the middle of the parent */
  transform: translate(-50%, -50%);
  overflow: auto;
}

.modal .item-container ul {
display: inline-block;
vertical-align: top;
margin-left: 30px;
}

.modal .item-container ul li{
padding-bottom: 10px;
  }
  

.modal .item-container input , .submit-button {
  display: block;
  width: 459px;
  padding: 5px;
  margin-bottom: 15px;
  border: 1px solid  #25362880;
  border-radius: 4px;
  outline: none;
  }

.submit-button { 
  background-color: #5656d4;
  width: 471px;
}

.modal .item-container input:focus {
  border: 1px solid #5656d4;
  }

.modal .item-container img {
  display: inline-block;
  width: 471px;
  height: 317px;
  margin-bottom:  15px;
    }


.modal-dubl {
  position: fixed; /* Stay in place */
  z-index: 2; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; 
  background-color: rgba(0, 0, 0, 0.5);
}


@media (max-width: 1000px) {
  .gallery-list {
    grid-template-columns: repeat(2, 300px);
    justify-content: center;
  }
}




@media (max-width: 500px) {
  .gallery-list {
    grid-template-columns: repeat(1, 300px);
    justify-content: center;
  }
  .modal .item-container {
    width: 100%;
    height: 100%;
    padding: 0;
    
}
.modal .item-container input , .submit-button {
width: 84%;
margin-left: auto;
margin-right: auto;
  }

  .modal .item-container img {
    width: 100%;
      }
}